@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,200;0,500;0,600;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Santa {
  display: relative;
  margin-bottom: 5vh;
  margin-top: 5vh;
}
.Santa-img {
  width: 10vw;
  height: auto;
}

.Santa-speechbubble {
  margin-top: 15px;
  position: relative;
  width: 30em;
  max-width: 30em;
  background-color: #fff;
  padding: 1.125em 1.5em;
  border-radius: 1rem;
  box-shadow: 0 0.125rem 0.5rem rgba(0, 0, 0, 0.3),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.2);
  margin-left: auto;
  margin-right: auto;
}

.Santa-speechbubble.new-message {
  animation: breathing 1s ease-in normal 1;
}

.Santa-speechbubble::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  bottom: 100%;
  border: 0.75rem solid transparent;
  border-top: none;
  border-bottom-color: #fff;
  -webkit-filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, 0.1));
          filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, 0.1));
  left: 50%;
  transform: translateX(-50%);
}

@keyframes breathing {
  0% {
    transform: scale(.8);
  }

  25% {
    transform: scale(1);
  }

  60% {
    transform: scale(1);
  }

  100% {
    transform: scale(1);
  }
}

.LookupForm-form {
  display: flex;
  max-width: 25rem;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20vh;
  margin-bottom: 0.5rem;
  border-radius: 0.8rem;
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.05);
}

.LookupForm-input {
  width: 100%;
  flex-grow: 1;
  border-top-left-radius: 0.8rem;
  border-bottom-left-radius: 0.8rem;
  border: none;
  font-size: 1rem;
  padding: 1rem 2rem;
  -webkit-writing-mode: horizontal-tb !important;
  text-rendering: auto;
  color: -internal-light-dark(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
  background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
  -webkit-rtl-ordering: logical;
  cursor: text;
  margin: 0em;
  font-family: inherit;
}

.LookupForm-button {
  padding: 20px;
  font-size: 0.9rem;
  border-top-right-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
  background-image: linear-gradient(45deg, #fb7f3b, #d4145a);
  flex-grow: 1;
  width: 8rem;
  -webkit-appearance: button;
     -moz-appearance: button;
          appearance: button;
  -webkit-writing-mode: horizontal-tb !important;
  text-rendering: auto;
  color: -internal-light-dark(black, white);
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: default;
  background-color: -internal-light-dark(rgb(239, 239, 239), rgb(59, 59, 59));
  box-sizing: border-box;
  margin: 0em;
  font: 400 13.3333px Arial;
  padding: 1px 6px;
  border-width: 2px;
  border-style: outset;
  border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
  border-image: none;
  border-image: initial;
  cursor: pointer;
  font-family: inherit;
  text-decoration: none;
  color: #fff;
  border: none;
  text-transform: uppercase;
}

.LookupForm-button:disabled {
  background-image: linear-gradient(45deg, #aaa, #bbb);
  cursor: not-allowed;
}

.LookupForm-button:focus {
  outline: none;
}

.LookupForm-input:focus {
  outline: none;
}

.Footer {
  font-size: 13px;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

body {
  background-color: #f0f3f7;
  font-family: 'Barlow', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  text-decoration: none;
  color: #0D4F8B;
}
a:visited {
  color: #0D4F8B;
  text-decoration: none;
}
